'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { appointmentsAPI, Appointment } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';

export default function AppointmentDetailPage() {
  const params = useParams<{ id: string }>();
  const router = useRouter();
  const [appt, setAppt] = useState<Appointment | null>(null);
  const [status, setStatus] = useState<Appointment['status']>('scheduled');
  const [notes, setNotes] = useState('');

  useEffect(() => { (async () => { const res = await appointmentsAPI.get(params.id); setAppt(res.data); setStatus(res.data.status); setNotes(res.data.notes || ''); })(); }, [params.id]);

  const save = async () => {
    try { await appointmentsAPI.update(params.id, { status, notes }); router.push('/dashboard/appointments'); } catch (e) { /* handle */ }
  };

  if (!appt) return <div>Loading...</div>;

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Appointment</h2>
        <Button variant="outline" onClick={() => router.push('/dashboard/appointments')}>Back</Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Details</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input label="Status" value={status} onChange={(e) => setStatus(e.target.value as any)} />
            <Input label="Notes" value={notes} onChange={(e) => setNotes(e.target.value)} />
          </div>
          <div className="mt-4 flex space-x-3">
            <Button onClick={save}>Save</Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
